import React, {
  useCallback, useMemo, useRef, useState
} from 'react';
import {
  Box, Button, Center, Checkbox, Img, Text
} from '@chakra-ui/react';
import { useTranslation } from 'react-i18next';
import style from './CheckInput.module.css';

type Props = React.PropsWithChildren<{
    title: string;
    file?: File | null;
    onChange: (file: File | null) => void
}>

const CheckInput = ({
  title, file, children, onChange
}: Props) => {
  const { t } = useTranslation();
  const [fileData, updateFileData] = useState<File | null>(file || null);
  const fileInput = useRef<HTMLInputElement>(null);

  const fileOnChange = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length !== 0) {
      const f = e.target.files[0];
      updateFileData(f);
      onChange(f);
    } else if (!fileData) {
      updateFileData(null);
      onChange(null);
    }
  }, [fileData]);

  const imgUrl = useMemo(() => (fileData === null ? '' : URL.createObjectURL(fileData)), [fileData]);

  return (
    <Box className={style.container}>
      <input
        accept="image/*,application/pdf"
        className="hidden-file"
        onChange={fileOnChange}
        type="file"
        style={{ display: 'none' }}
        ref={fileInput}
      />
      <Box display="flex">
        <Checkbox size="lg" isChecked={!!fileData} pointerEvents="none">
          <Text variant="regular" ml="8px" fontSize="16px">{title}</Text>
        </Checkbox>
        {fileData ? <Text variant="small" textDecoration="underline" marginLeft="auto" fontSize="14px" cursor="pointer" className="text-light" onClick={() => fileInput.current?.click()}>{t('deposit_check_requirements.reUpload')}</Text> : null}
      </Box>

      {!fileData ? (
        <>
          {children}
          <Button width="100%" variant="primary" onClick={() => fileInput.current?.click()}>{t('deposit_check_requirements.upload')}</Button>
        </>
      ) : (
        <Center pt="24px">
          <Img src={imgUrl} alt="check image" data-testid="card-img" />
        </Center>
      )}

    </Box>
  );
};

export default CheckInput;
