import React, { useCallback, useMemo, useState } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, Input, ListItem, FormControl, FormLabel, UnorderedList, Button,
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import MiniBox from '../../../layout/MiniBox';
import style from './DepositCheckRequirements.module.css';
import CheckInput from '../../../components/CheckInput';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import { updateCheckDepositData } from '../../../store/actionCreators/checkpayment';
import CurrencyInput from '../../../components/CurrencyInput';
import { currencyFormat } from '../../../utils/general';
import { useDrawer } from '../../../hooks/drawer';

const CheckDepositPreview = React.lazy(() => import('../CheckDetailsConfirm/CheckDetailsConfirm'));
/**
 * Check deposit form page
 * @returns {React.ReactNode}
 */
const DepositCheckRequirements = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const { open } = useDrawer();

  const selectedAccount = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const selectedContact = useAppSelector((store) => store.contact.selectedContact, shallowEqual);
  const checkData = useAppSelector((store) => store.checkdeposit.checkData, shallowEqual);

  const [frontImage, setFrontImg] = useState<File | null>(checkData.frontImage);
  const [backImage, setBackImg] = useState<File | null>(checkData.backImage);
  const [amount, setAmount] = useState<string>(checkData.amount);

  const maxAmount = useMemo(() => {
    let limit = selectedAccount?.config.limits.receive.check.daily || 0.00;
    if (limit === 0.00 || limit === '') {
      limit = selectedAccount?.config.limits.receive.daily || 1000;
    }
    return typeof limit === 'string' ? parseFloat(limit) : limit;
  }, []);

  const isValid = useMemo(() => (frontImage && backImage && amount.length !== 0 && parseFloat(amount) <= maxAmount), [maxAmount, frontImage, backImage, amount]);

  // eslint-disable-next-line @typescript-eslint/no-shadow
  const updateCheckData = useCallback(() => {
    if (isValid) {
      dispatch(updateCheckDepositData({
        frontImage,
        backImage,
        amount
      }));
      setTimeout(() => {
        open(CheckDepositPreview);
      }, 0);
    }
  }, [frontImage, backImage, amount, isValid]);

  return (
    <div className="page">
      <MiniBox
        bodyClass={style.bodyStyles}
        heading={t('deposit_check_requirements.title')}
        footer={(
          <Button width="100%" variant="primary" disabled={!isValid} onClick={updateCheckData}>
            {t('deposit_check_requirements.ctaText')}
          </Button>
          )}
      >
        <Box width="100%" paddingTop="15px">
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('deposit_check_requirements.payorName')}
            </FormLabel>
            <Input
              variant="primary"
              readOnly
              value={selectedContact?.name}
            />
          </FormControl>
          <FormControl variant="primary">
            <FormLabel variant="primary">
              {t('deposit_check_requirements.enterAmount')}
            </FormLabel>
            <CurrencyInput
              value={amount}
              onChange={(value) => {
                setAmount(value);
              }}
            />

            <Text variant="small" pt="10px" textAlign="left" className="text-light">
              {t('deposit_check_requirements.maximAmount', { amount: currencyFormat(maxAmount) })}
            </Text>
          </FormControl>

          <Text variant="large" className={style.addCheckImagesTitle}>{t('deposit_check_requirements.addCheckImages')}</Text>

          <CheckInput
            title={t('deposit_check_requirements.frontOfCheck')}
            onChange={setFrontImg}
            file={frontImage}
          >
            <UnorderedList className={style.listRequirements}>
              <ListItem><Text variant="regular" className={`${style.requireInfo} text-light`}>{t('deposit_check_requirements.frontInfo1')}</Text></ListItem>
              <ListItem><Text variant="regular" className={`${style.requireInfo} text-light`}>{t('deposit_check_requirements.frontInfo2')}</Text></ListItem>
              <ListItem><Text variant="regular" className={`${style.requireInfo} text-light`}>{t('deposit_check_requirements.frontInfo3')}</Text></ListItem>
            </UnorderedList>
          </CheckInput>

          <CheckInput
            title={t('deposit_check_requirements.backOfCheck')}
            onChange={setBackImg}
            file={backImage}
          >
            <UnorderedList className={style.listRequirements}>
              <ListItem><Text variant="info" className={`${style.requireInfo} text-light`}>{t('deposit_check_requirements.backInfo1')}</Text></ListItem>
              <ListItem><Text variant="info" className={`${style.requireInfo} text-light`}>{t('deposit_check_requirements.backInfo2')}</Text></ListItem>
              <ListItem><Text variant="info" className={`${style.requireInfo} text-light`}>{t('deposit_check_requirements.backInfo3')}</Text></ListItem>
              <ListItem><Text variant="info" className={`${style.requireInfo} text-light`}>{t('deposit_check_requirements.backInfo4')}</Text></ListItem>
            </UnorderedList>
          </CheckInput>

        </Box>
      </MiniBox>

    </div>
  );
};

export default DepositCheckRequirements;
